import {createStore, applyMiddleware} from 'redux';
import logger from 'redux-logger';

import treeStatsApp from './reducers';

const middleware = applyMiddleware(logger());

/**
 * creates the store with the preload_state
 * @param {any} preload_state
 * @return {Store}
 */
export default function configureStore(preload_state) {
    return createStore(
        treeStatsApp,
        middleware,
    );
};



