export const initial_filter = {
    limit: 15000,
};

export const initial_tree_conf = {
    start: false,
    scionPathOf: false,
    ascendancy: true,
    mastery: false,
};

/**
 * object for react-redux-form createForms
 * @type {Object}
 */
const forms = {
    data_filter: initial_filter,
    passive_tree_conf: initial_tree_conf,
};

export default forms;



