import React from 'react';

import Node from './Node.jsx';

/**
 *
 */
class Nodes extends React.Component {
    /**
     * @param {PassiveNode} node
     * @return {boolean} true if the node should be drawn under the current conf
     */
    isVisible(node) {
        const {conf} = this.props;

        return (conf.mastery || !node.mastery)
            && (conf.start || !node.start)
            && (conf.ascendancy || !node.ascendancy);
    };

    * drawn() {
        const {nodes} = this.props;

        for ([node_id, node] of nodes.entries()) {
            if (this.isVisible(node)) {
                yield [node_id, node];
            }
        }
    }

    /**
     * @return {JSX}
     */
    render() {
        return (
            <g className="passive-nodes">
                {[...this.drawn()]
                    .map((entry) => {
                        const [node_id, node] = entry;
                        return (
                            <Node key={node_id} node={node} />
                        );
                    })}
            </g>
        );
    }
};

export default Nodes;



