import React from 'react';

/**
 *
 */
class Node extends React.Component {
    /**
     * @return {JSX}
     */
    render() {
        const {node} = this.props;
        const {x, y, size, types} = node;
        const class_name = [...types, 'tree_node'].join(' ');

        return (
            <circle cx={x} cy={y} r={size} className={class_name} />
        );
    }
};

export default Node;



